import React from 'react'
import PropTypes from 'prop-types'
import { List, Toast, SearchBar, Picker } from 'antd-mobile';
import { Link } from 'react-router';
import axios from '../axios'
import { checkToken } from '../check'
import { aesencode, aesdecode }  from '../utils';

const Item = List.Item;
const Brief = Item.Brief;
class Merchants extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: false,
            searchkey: '',
            apiurl: props.apiurl,
            userinfo: props.userinfo,
            pageNumber: 1,
            pageSize: 10,
            totalPage: 1,
            totalNum: 0,
            totalMoney: 0,
            placeholder_img: require('../assets/img/nodata.png'),
            statistic_img: require('../assets/img/statistic.png'),
            userOptions: []
        }
    }

    componentWillMount(){
        document.title = '用户列表';
    }

    getData(){
        let that = this;
        if(!this.state.loading) {
            Toast.loading('加载中…', 30);
            this.setState({
                loading: true
            });
            checkToken(function () {
                axios.get('/admins/children', {
                    params: {
                        page : that.state.pageNumber,
                        num: that.state.pageSize,
                        search: '',
                        privilege: encodeURIComponent(aesencode('read')),
                        path: encodeURIComponent(aesencode('admin/myagents'))
                    }
                })
                .then(function (response) {
                    Toast.hide();
                    if (response.status == 200 && response.data.code == 0) {
                        let oData = that.state.data;
                        let nData = oData.concat(response.data.data.data);
                        that.setState({
                            loading: false,
                            totalPage: Math.ceil(response.data.data.total/that.state.pageSize),
                            totalNum: response.data.data.total,
                            totalMoney: response.data.data.all_money,
                            data: nData
                        });
                    }
                    else{
                        that.setState({
                            loading: false,
                            pageNumber: that.state.pageNumber - 1
                        });

                        Toast.fail(response.data.message, 2);
                    }
                })
                .catch(function (error) {
                    Toast.hide();
                    Toast.fail('未知错误', 2);
                    that.setState({
                        loading: false,
                        pageNumber: that.state.pageNumber - 1
                    });
                });
            });
        }
    }

    scroll(){
        var obj = document.querySelector(".container");
        var screenHeight = obj.offsetHeight;   //屏幕可见高度
        var scrollHeight = obj.scrollHeight;   //所有内容总高度
        let scrollTop = document.querySelector(".container").scrollTop;   //滚动条距离顶部高度

        //滚动条触底触发事件
        if(scrollHeight - screenHeight - scrollTop < 100){
            if(!this.state.loading && this.state.pageNumber < this.state.totalPage) {
                this.setState({
                    pageNumber: this.state.pageNumber + 1
                });

                this.getData();
            }
        }
    }

    componentDidMount(){
        var contentObj = document.querySelector(".container");
        contentObj.addEventListener('scroll', this.scroll.bind(this));
        this.getData();
    }

    render() {
        if(this.state.data && this.state.data.length > 0) {
            return (
                <div className="orderlist">
                    <div className="total_users">
                        <div className="left">共有商家{this.state.totalNum}个</div>
                        <div className="right">总营业额{this.state.totalMoney}元</div>
                    </div>
                    <List className="my-list">
                        {this.state.data.map((item, index) => {
                            return (
                                <Item key={index} arrow="horizontal" multipleLine>
                                    <div className="title list-title">{item.name}</div>
                                    <Brief>注册时间：{item.created_at.substring(0,10)}，设备数：{item.device_num}，维护人：{item.maintenance}</Brief>
                                    <Brief>激活数：{item.active_num}，激活率：{item.active_device_rate}%，营业额：{item.operate_money}</Brief>
                                    <Brief>24H激活率：{item.recent_active_rate}%，24H使用率：{item.recent_use_rate}%，分成：{item.discount}%</Brief>
                                </Item>
                            )
                        })}
                    </List>

                    <div className='nodata2' style={{display: !this.state.loading ? 'block' : 'none'}}>{this.state.pageNumber == this.state.totalPage ? '我是有底线的' : '上拉加载更多'}</div>
                    <div className="load-more" style={{display: this.state.loading && this.state.pageNumber != 1 ? 'block' : 'none'}}>
                        <div className="la-fire la-dark">
                            <div className='load-item'></div>
                            <div className='load-item'></div>
                            <div className='load-item'></div>
                        </div>
                    </div>
                    <div className='link'>
                        <Link to='/statistic'>
                            <img src={this.state.statistic_img}/>
                        </Link>
                    </div>
                </div>
            );
        }
        else if(!this.state.loading){
            return (
                <div className="orderlist">
                    <div className="nodata">
                        <img src={this.state.placeholder_img}/>
                        <div className="notxt">亲，还没有数据</div>
                    </div>
                </div>
            );
        }
        else{
            return (
                <div className="nodata"></div>
            );
        }
    }
}

Merchants.propTypes = {
    apiurl: PropTypes.string.isRequired,
    userinfo: PropTypes.object.isRequired
}

export default Merchants




